<template>
	<div>
		<el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="TRENDS" name="/trends">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="ENTRIES" name="/entries">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="SUGGESTIONS" name="/suggestions">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="ALERTS" name="/alerts">
      <router-view></router-view>
    </el-tab-pane>
  </el-tabs>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        activeName: 'second',
        newpath: 'default',
        name: ''
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(this.activeName)
        console.log(this.$route.path.split('/')[1])
        name = this.$route.path.split('/')[1]
        this.newpath = '/' + name + this.activeName
        this.$router.push({path: this.newpath})
      }
    },
    created() {
      console.log(this.$route.path)
      if(this.$route.path.split('/').length < 3) {
        this.newpath = this.$route.path + '/trends'
        this.$router.push({path: this.newpath})
      }
      this.activeName = '/' + this.$route.path.split('/')[2]
    },
    watch: {
      '$route' (to, from) {
      if(this.$route.path.split('/').length < 3) {
        this.newpath = this.$route.path + '/trends'
        this.$router.push({path: this.newpath})
      }
      this.activeName = '/' + this.$route.path.split('/')[2]
    }
   },
  };
</script>

<style>
  .el-tabs__item {
    width: 140px !important;
    padding: 0px !important;
    text-align: center;
    letter-spacing: 1px;
    font-weight: bold;
  }
  .el-tabs__active-bar {
    width: 140px !important;
  }
</style>


